import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store =new Vuex.Store({
    state:{
        logined:false,
        loginname:'',//
        accesstoken:'',//token
        loginedMoreMenus:{
          center:"",
          msg:"",
          logout:''
        },
        unLoginedMoreMenus:{
          login:''  
          // 
        },
        isShowMoreMenus:false,
        moreMenus:{
          menu1:'login'
        },
        topics:[
          {name:'all',desc:''},
          {name:'good',desc:''},
          {name:'share',desc:''},
          {name:'ask',desc:''},
          {name:'job',desc:''},
          {name:'test',desc:''},
        ]
    },
    getters:{  
        moreMen(state){
        // statestorestate
        if(state.logined){
          return state.loginedMoreMenus
        }
        return state.unLoginedMoreMenus;
      },
    },
    mutations:{
        mutationsisShowMoreMenus:function(state,payload){
            // 
            state.isShowMoreMenus=payload;
        },
        // 
        mutationIslogined:function(state){
          state.logined=true;
        },
        //
        mutationsloginname:function(state,payload){
          state.loginname=payload;
        },
        // token
        mutationstoken:function(state,payload){
          state.accesstoken=payload;
        },
        // 
        mutationslogout:function(state){
          state.logined=false;
          state.loginname='';
          state.accesstoken='';

        }
    },
    actions:{}
})
export default store;


