
import {mapState } from 'vuex';
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Emotion from 'vux/src/components/wechat-emotion/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
name:'topic',

props:['id'],
components:{
CellBox,Group,Flexbox,FlexboxItem,Emotion,Divider
},
computed:{
...mapState(['logined'])
},
data:function(){
return {
topic :null,

}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){
vm.$http.get("/topic/"+to.params.id).then(function(res){
vm.topic = res.data.data;
console.log(vm.topic);
})
})
}
}



