
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import Scroller from 'vux/src/components/scroller/index.vue'

export default {
props: ["name"],
data: function () {
return {
lists: [],
page: 1, //
limit: 5, //
pullupConfig: {

content: "",
downContent: "",
upContent: "",
loadingContent: "...",
},
pulldownConfig: {

content: "",
downContent: "",
upContent: "",
loadingContent: "...",
},
};
},
methods: {
freshData() {
var app = this;
this.page = this.page + 1;
this.$http
.get("/topics", {
params: {
tab: this.name,
page: 1,
limit: this.limit,
},
})
.then(function (res) {

app.lists=res.data.data;



app.$refs.pullup.donePulldown();
});
},
loadMoreData: function () {
var app = this;
this.page = this.page + 1;
this.$http
.get("/topics", {
params: {
tab: this.name,
page: this.page,
limit: this.limit,
},
})
.then(function (res) {

for (var i = 0; i < res.data.data.length; i++) {
app.lists.push(res.data.data[i]);
}



app.$refs.pullup.donePullup();
});
},
},
components: {
Cell,
Group,
Scroller,
},



beforeRouteEnter: function (to, from, next) {



next(function (v) {
v.$http
.get("/topics", {
params: {
tab: to.params.name,
page: v.page,
limit: v.limit,
},
})
.then(function (r) {
if (r.data.success == true) {
v.list = r.data.data;
}
});
});
},
beforeRouteUpdate: function (to, from, next) {


var app = this;
this.$http
.get("/topics", {
params: {
tab: to.params.name,
page: this.page,
limit: this.limit,
},
})
.then(function (r) {
if (r.data.success == true) {
app.lists = r.data.data;
}
});
},
};



