
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mapMutations} from 'vuex';
export default {
name:"login",
components:{
Group,XInput,XButton
},
data:function(){
return {
accesstoken:''
}
},
methods:{
...mapMutations(['mutationIslogined','mutationsloginname','mutationstoken']),
dologin(){
var app=this;
this.$http.post('/accesstoken',{
accesstoken:this.accesstoken
}).then(function(r){

app.mutationIslogined();
app.$router.push('/user/'+r.data.loginname);
app.mutationstoken(app.accesstoken);

app.mutationsloginname(r.loginname);


window.localStorage.setItem('loginStatus',true);
window.localStorage.setItem('loginname',r.data.loginname)
window.localStorage.setItem('loggintoken',app.accesstoken)
})
},

}

}



