
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import {mapState,mapGetters,mapMutations} from 'vuex'
export default {
name: 'app',
created:function(){

var loginStatus= window.localStorage.getItem('loginStatus');

if(loginStatus){
this.mutationIslogined();
this.mutationsloginname(window.localStorage.getItem('loginname'))
this.mutationstoken(window.localStorage.getItem('loggintoken'))
}

},
computed:{
...mapState(['logined','loginedMoreMenus','unLoginedMoreMenus','isShowMoreMenus','moreMenus','topics']),
...mapGetters(['moreMen']),







isShowMoreMenus:{

get:function(){
return this.$store.state.isShowMoreMenus
},

set:function(nval){
this.$store.commit('mutationsisShowMoreMenus',nval)
}
}
},
data:function(){
return {





















}
},
methods:{
...mapMutations(['mutationIslogined',"mutationsloginname",'mutationstoken']),
handClick:function(menuKey, menuItem){


this.$router.push('/'+menuKey)
console.log(menuKey+"==="+menuItem);

},

showMoreMenus:function(){
this.isShowMoreMenus=true;
},
routeto:function(index){

this.$router.push("/topiclist/"+this.topics[index].name)
}
},
components: {
Tab,
TabItem,
XHeader,
Actionsheet
}
}



